
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'


export default {
components: {
Group, Cell, XButton, Popup, XDialog, XInput, Loading, XTextarea
},
props: ['show'],
data() {
return {
isShow: this.show,
workers: [],
user_info: {
show: false,
user: ''
},
dialogShow: false,
reject: {
data: '',
user_id: ''
},
loadings:true
}
},
methods: {
getExamineWorkers() {
axios.post('api/worker/getWaitExamineWorker', {enterprise_id: 1}).then(response => {
this.workers = response.data.data.data
this.loadings = false
}).catch(error => {
this.loadings = false
})
},
showUserInfo(user) {
this.user_info.user = user
console.log(user)
this.user_info.show = true
},
actionReject() {
axios.post('api/worker/reject', {user_id: this.user_info.user.id, content: this.reject.data}).then(response => {
this.$vux.alert.show({
content: response.data.message
})
}).catch(error => {
this.$vux.alert.show({
content: error.response.data.message
})
})
},
agree(user_id) {
let _this = this
this.$vux.confirm.show({
content: '',
onConfirm() {
axios.post('api/enterprise/agreeWorkerJoin', {user_id: user_id}).then(response => {
_this.getExamineWorkers()
_this.$emit('change')
}).catch(error => {

})
}
})
}
},
watch: {
show () {
this.isShow = this.show
},
},
mounted() {
this.getExamineWorkers()
}
}



