
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {
components: {
Search, Group, Cell, Loading
},
props: ['show'],
data() {
return {
isShow: this.show,
workers: [],
search: {
data: '',
auto_fixed: false
},
loadings:true
}
},
methods: {
getWorkers() {

axios.post('api/worker/all', {enterprise_id: 1}).then(response => {
this.workers = response.data.data.data
this.loadings = false
}).catch(error => {
this.loadings = false
})
},
searchWorker(name) {
this.loadings = true;
axios.post('api/worker/getByName', {enterprise_id: 1, name: name}).then(response => {
this.workers = response.data.data
this.loadings = false
}).catch(error => {
this.loadings = false;
})
},
toUserHealth(user_id) {
this.$router.push('/health/' + user_id)
},
clearSearch() {
this.getWorkers()
}
},
mounted() {
this.getWorkers()
},
watch: {
show () {
this.isShow = this.show
},
}
}



